*{
    padding: 0;
    margin:0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;

}

h2{
    font-weight: 700;
}

.main-layout{
    display:grid;
    grid-template-columns: 60% auto;
    align-items: center;
}

.forms-container{
    background-color: #1a1f3f;
    display: flex;
    flex-direction: column;

}

.container{
    padding: 20px;
}

.container form input{
    width: 75%;
    height: 36px;
    font-weight: 500;
    background-color: #1a1f3f;
    border:1px solid #eef0fd;
color:#eef0fd;
    border-radius: 5px;
    outline: none;
    font-size: 18px;
    padding: 10px;
}

.container button{
    display: block;
    background-color: #3e3eb7;
    outline:none;
    font-size: 16px;
    border: none;
    color:#eef0fd;
    padding:8px 30px;
    border-radius: 4px;
    margin-top: 30px;
    -webkit-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
}

.container form label p{
    font-size: 19px;
    color:#eef0fd;
    margin:20px 0px 6px 0px;
}


body{
    background-color: #1a1f3f;
}

.container h2{
    color:#e9eaf5;
    font-size:30px;
   margin-bottom: 24px;
   font-weight: 700;
}

.btns{
    display: flex;
    gap:30px;
    align-items: center;

}

.go-ahead{
    display: flex;
    padding: 20px;
    align-items: center;
    position: relative;
    justify-content: space-between;
}

.go-ahead .circle{
    z-index:20;
    padding:5px;
    background-color:#eef0fd;
    border-radius: 100%;
    -webkit-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
}

.go-ahead .live{
    background-color: #255acc !important;
    -webkit-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
}

.go-ahead .circle img{
    max-width: 45px;
    max-height: 45px;
}

.go-ahead::after{
    content: '';
    position: absolute;
    top:50%;
    padding: 3px;
    width: 80%;
    left: 30px;
    z-index:0;
    background-color: antiquewhite;
}

.finish{
    justify-self: center;
    align-self: center;
}

.finish button{
padding:8px 26px;    display: block;
background-color: #b7753e;
outline:none;
font-size: 16px;
outline: none;
border: none;
color:#eef0fd;
border-radius: 5px;
}

.radio{
    display: flex;
    justify-content: start;
    align-items: center;
    gap:40px;
}

.radio img{
    max-width: 60px;
    max-height: 60px;
}

.sideimg img{
    height: 100vh;
    width: 100%;
}

.finished h1{
    color:#255acc;
    font-weight: 700;
    font-size: 80px;
    padding: 20px;
}

.finished p{
    color:#eef0fd;
    font-size: 40px;
    padding: 0px 20px;
}

.finished p span{
    color:#255acc;
    font-weight: 700;
}